import React from 'react';
import { Layout, Menu } from 'antd';
import { HomeOutlined, UserOutlined, ProjectOutlined } from '@ant-design/icons';
import { Switch, Route, Redirect, useLocation, useHistory } from 'react-router-dom';
import { Main, Project, EditProject, CreateProject, EditUser, CreateUser, User } from 'pages';
import { UserBlock, LogoutButton } from 'components';
import Logo from 'brand_logo.svg';
//Redux
import { useSelector } from 'react-redux';

const { Header, Content, Sider } = Layout;

const Home = ({ onSuccessLogout }) => {
	let location = useLocation();
	let history = useHistory();

	const { userInfo } = useSelector(({ user }) => user);
	const [collapsed, setCollapsed] = React.useState(false);

	const isAdmin = userInfo.role === 0;

	const menuItems = [
		{ label: 'Главная', key: '/', icon: <HomeOutlined /> },
		isAdmin
			? {
					label: 'Проекты',
					key: 'projects',
					icon: <ProjectOutlined />,
					children: [
						{ label: 'Список проектов', key: '/projects' },
						{ label: 'Создать проект', key: '/projects/create' },
					],
			  }
			: false,
		isAdmin
			? {
					label: 'Пользователи',
					key: 'users',
					icon: <UserOutlined />,
					children: [
						{ label: 'Список пользователей', key: '/users' },
						{ label: 'Создать пользователя', key: '/users/create' },
					],
			  }
			: false,
	];

	const onMenuSelect = ({ key }) => {
		history.push(key);
	};

	const handleChangeCollapse = (value) => {
		setCollapsed(value);
		localStorage.setItem('siderCollapsed', value);
	};

	return (
		<Layout>
			<Header>
				<div className="logo">
					<img src={Logo} alt="company-logo" />
					{/* <h1>Админ раздел</h1> */}
				</div>
				<div className="d-flex">
					<UserBlock />
					<LogoutButton onLogout={onSuccessLogout} />
				</div>
			</Header>
			<Layout>
				<Sider
					width={210}
					collapsible
					collapsed={collapsed}
					onCollapse={handleChangeCollapse}>
					<Menu
						selectedKeys={[location.pathname]}
						mode="inline"
						className="menu"
						items={menuItems}
						onSelect={onMenuSelect}
					/>
				</Sider>
				<Layout>
					<Content>
						<Switch>
							<Route exact path="/">
								<Main />
							</Route>
							{
								//Админ
								isAdmin && (
									<Switch>
										<Route exact path="/projects">
											<Project />
										</Route>
										<Route exact path="/projects/:id/edit">
											<EditProject />
										</Route>
										<Route exact path="/projects/create">
											<CreateProject />
										</Route>
										<Route exact path="/users">
											<User />
										</Route>
										<Route exact path="/users/:id/edit">
											<EditUser />
										</Route>
										<Route exact path="/users/create">
											<CreateUser />
										</Route>
										<Redirect to="/" />
									</Switch>
								)
							}
							<Redirect to="/" />
						</Switch>
					</Content>
				</Layout>
			</Layout>
		</Layout>
	);
};

export default Home;
